<template>
    <div>
        <el-container>
            <el-aside width="232px" style="height: 105vh;">
                <p
                    style="font-weight: 600;font-size: 17px;margin-top: 12px;width: 200px;margin-left: -12px;margin-bottom: 8px;">
                    超级企业列表({{ numbers }})
                </p>
                <!-- 输入框 -->
                <el-input v-model="input" style="width: 135px; height: 50px;margin-right: 9px;" placeholder="请输入内容"
                    @input="searchfun()"></el-input>
                <!-- 添加按钮 -->
                <template>
                    <div style="display: inline-block">
                        <!-- 按钮触发弹窗 -->
                        <el-button type="text" class="btn" @click="dialogVisible = true">
                            <i class="el-icon-plus" style="font-size: 18px;"></i>
                        </el-button>
                        <!-- 弹窗内容 -->
                        <el-dialog title="新增超级企业" :visible.sync="dialogVisible" width="45%" @close="resetForm">
                            <el-form ref="addForm" :model="addForm" :rules="rules" label-width="150px">
                                <el-form-item label="超级企业名称" prop="ename" style="margin-bottom: 30px;">
                                    <el-input v-model="addForm.ename"></el-input>
                                </el-form-item>
                                <el-form-item label="企业域名" prop="ipstr" style="margin-bottom: 30px;">
                                    <el-input v-model="addForm.ipstr"></el-input>
                                </el-form-item>
                                <el-form-item label="超级管理员账号" prop="adminname" style="margin-bottom: 30px;">
                                    <el-input v-model="addForm.adminname"></el-input>
                                </el-form-item>
                                <el-form-item label="邮箱" prop="adminemil" type="email" style="margin-bottom: 30px;">
                                    <el-input v-model="addForm.adminemil"></el-input>
                                </el-form-item>
                                <el-form-item label="最大座席数" prop="maxnum" style="margin-bottom: 30px;">
                                    <el-input v-model="addForm.maxnum"></el-input>
                                </el-form-item>
                                <el-form-item label="最大登录并发总数" prop="contentmaxlogin" style="margin-bottom: 30px;">
                                    <el-input v-model="addForm.contentmaxlogin"></el-input>
                                </el-form-item>
                            </el-form>
                            <span slot="footer" class="dialog-footer">
                                <el-button @click="dialogVisible = false" class="buttona">取消</el-button>
                                <el-button type="primary" @click="submitForm('addForm')" class="buttona">添加</el-button>
                            </span>
                        </el-dialog>
                    </div>
                </template>

                <ul>
                    <li v-for="(item, index) in kong" :key="index" @click="ac(item)">
                        <div :class="item.status == 1 ? 'green' : 'red'"
                            :style="{ backgroundColor: boxBackgroundColor }"></div>{{
                                item.ename }}
                    </li>
                </ul>
            </el-aside>
            <el-container>
                <el-main>
                    <div
                        style="width: 95%;height: 100vh; background-color: white;margin: auto;margin-top: 24px;padding-left: 12px;overflow: hidden; text-align: left;padding-top: 12px;">
                        <el-radio-group v-model="tabPosition"
                            style=" float: left; margin-left: 10px; margin-top: 5px;width: 100%;height: 40px; margin-bottom: 12px;">
                            <router-link to="/shouye/qiyeguanli/chaoji" tag="span">
                                <el-button label="top" type="primary" plain class="buttoncla">
                                    超级企业管理
                                </el-button>
                            </router-link>
                            <router-link to="/shouye/qiyeguanli/ziguanli" tag="span">
                                <el-button label="right" type="primary" plain class="buttoncla">
                                    子企业管理
                                </el-button>
                            </router-link>
                        </el-radio-group>
                        <router-view @a="pp()"></router-view>
                    </div>
                </el-main>
            </el-container>
        </el-container>
    </div>
</template>
<script>
import { mapState } from 'vuex'
import { getsuperes, getsearches, getxiangqing, addsuper } from "../api/html.js";
export default {
    data() {
        return {
            dialogVisible: false, // 控制弹窗显示隐藏的标志
            addForm: { // 表单数据对象
                ename: '',
                ipstr: '',
                adminname: '',
                adminemil: '',
                maxnum: '',
                contentmaxlogin: ''
            },
            rules: { // 表单验证规则
                ename: [
                    { required: true, message: '请输入超级企业名称', trigger: 'blur' },
                ],
                ipstr: [
                    { required: true, message: '请输入企业域名', trigger: 'blur' },
                ],
                adminname: [
                    { required: true, message: '请输入超级管理员账号', trigger: 'blur' },
                ],
                adminemil: [
                    { required: true, message: '请输入邮箱', trigger: 'blur' },
                ],
                maxnum: [
                    { required: true, message: '请输入最大座席数', trigger: 'blur' },
                ],
                contentmaxlogin: [
                    { required: true, message: '请输入最大登录并发总数', trigger: 'blur' },
                ],
            },
            tabPosition: 'left',
            input: '',
            kong: [],
            selectedItem: {} // 用于同步显示的数据，初始化为空对象
        }
    },
    computed: {
        ...mapState(['boxBackgroundColor']),
        numbers() {
            return this.kong.length
        },
        // vuex方法
        ...mapState({
            ArrData: state => state.arr
        })
    },
    // 页面渲染完成后执行
    mounted() {
        this.show()
    },
    methods: {
        pp() {
            this.show()
        },
        // 显示对话框时重置表单
        resetForm() {
            this.$refs.addForm.resetFields();
        },
        // 添加
        addsuper(data) {
            addsuper(data).then(res => {
                console.log(res);
            })
        },
        // 表单验证
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    alert('添加成功!');
                    this.addsuper(this.addForm);
                    // 添加完毕后将输入框隐藏
                    this.dialogVisible = false;
                    // 添加完毕后自动刷新页面
                    this.show()
                } else {
                    console.log('error submit!!');
                    return true;
                }
            });
        },
        // 详情信息
        ac(data) {
            getxiangqing(data.id).then(res => {
                this.$store.dispatch('upnum', res.data)
                console.log(this.$store.state.arr);
            })
        },
        // 模糊搜索
        searchfun() {
            let obj = { ename: this.input }
            getsearches(obj).then(res => {
                this.kong = res.data.data
                console.log(this.kong);
            })
        },
        // 数据渲染
        show() {
            getsuperes().then(res => {
                this.kong = res.data.data
                console.log(this.kong);
            })
        },
        fetchData() {
            // 获取API数据的方法
            getsuperes().then(response => {
                this.kong = response.data; // 赋返回的数据
                // 初始化selected为第一个项目的数据，假设存在
                this.selected = this.kong.length > 0 ? this.kong[0] : {};
            });
        },
    },
    created() {
        this.fetchData(); // 页面加载时调用数据
        this.ac({ id: 67 })
    },
}
</script>
<style scoped>
* {
    margin: 0;
    padding: 0;
}

.buttona {
    width: 100px;
    height: 30px;
    border: none;
}

.buttoncla {
    width: 100px;
    height: 30px;
    text-align: center;
    line-height: 30px;
    background-color: white;
    border-radius: 0px;
    padding: 0;

}

.green {
    display: inline-block;
    width: 5px;
    height: 5px;
    border-radius: 50%;
    border: none;
    background-color: #38D88F;
    margin-right: 5px;
    margin-bottom: 3px;
}

.red {
    background-color: red;
    display: inline-block;
    width: 5px;
    height: 5px;
    border-radius: 50%;
    border: none;
    margin-right: 5px;
    margin-bottom: 3px;
}

li {
    list-style: none;
    border-bottom: 1px solid #F3F3F5;
    height: 40px;
    text-align: left;
    line-height: 40px;
    color: #9DADCA;
    padding-left: 14px;
}

li:hover {
    background-color: #ECF5FF;
}

.btn {
    width: 40px;
    height: 40px;
    border: none;
    background-color: #EAF6FE;
    color: #34A5FE;
}

.el-aside {
    background-color: #ffffff;
    color: #333;
    text-align: center;
}

.el-main {
    background-color: #F7F7F7;
    color: #333;
    text-align: center;
}

body>.el-container {
    margin-bottom: 40px;
}

.el-container:nth-child(5) .el-aside,
.el-container:nth-child(6) .el-aside {
    line-height: 260px;
}

.el-container:nth-child(7) .el-aside {
    line-height: 320px;
}
</style>
